<template>
  <div class="container-popular">
    <!-- 封面 -->
    <div class="covers">
      <img src="http://p3gbjsikb.bkt.clouddn.com/cover/collected.jpg" alt="">
    </div>
    <!-- 歌曲 -->
    <div class="all-list">
      <div class="all-1" v-for="(item,index) in songlist" :key="index">
        <div class="numbers">
          {{ index +1 }}
        </div>
        <div class="all-12" @click="_songPlay(item.songid)">
          <div class="text-15"><nobr>{{ item.title }}</nobr></div>
          <div class="text-13"><nobr>{{ item.author }}</nobr></div>
          <child :title="title"></child>
          <child :author="author"></child>             
        </div>
      </div>
    </div>
    <div class="blank"></div>
    <foot :isPlay="isPlay" :titleNow="titleNow" :authorNow="authorNow" :pic="pic" :songidNow="songidNow"></foot>
  </div>
</template>

<script>
import { getPopular } from '@/api/rank'
import foot from '@/components/footer'

export default{
  name: "allList" ,
  components :{
    foot
  },
  data:function () {
    return{
      bannerType: 1,
      street: '',
      songlist: [],
      songidNow: '',
      titleNow: '',
      authorNow: '',
      pic: '',
      isPlay: false
    };
  },
  methods:{
    _getPopular () {
      getPopular().then((res) => {
        if (res.code === 1) {
          this.songlist = res.data.a
          titleMsg = songlist.title
        }
      })
    },
    _songPlay(songid){
      this.songidNow = songid
      this.isPlay = true
    }
  },
  onShow: function() {
    this.isPlay = this.GLOBAL.isPlay
  },
  onLoad: function(options) {
    this.isPlay = this.GLOBAL.isPlay
    this.songidNow = options.songid
    this.titleNow =  options.title
    this.authorNow = options.author
    this.pic = options.pic
  },
  created(){ 
    this._getPopular();
  }
}
</script>

</script>

<style scoped>
.container-popular{
  width: 100%;
  color: #fff;
  font-family:SimSun;
}
.covers{
  padding: 0;
  margin-bottom: -5px;
}
.covers img{
  width: 100%;
}
.all-1{
  display: -webkit-box;
  opacity:0.8;
  filter:alpha(opacity=80);
  background:rgb(43, 44, 46);
  /* background: rgb(134, 126, 126);
  opacity: 0.7; */
}
.numbers{
  width: 40px;
  font-size: 18px;
  padding-top: 15px;
  text-align: center;
  color: rgb(170, 169, 169);
}
.all-12{
  padding: 6px 5px;
  width: 80%;
  border-bottom: 1px solid rgb(63, 62, 62);
}
.text-15{
  line-height: 23px;
  font-size: 15px;
  text-overflow: ellipsis;
  overflow: hidden;
  white-space: nowrap;
}
.text-13{
  font-size: 13px;
  text-overflow: ellipsis;
  overflow: hidden;
  white-space: nowrap;
}
.blank{
  height: 60px;
  background:rgb(43, 44, 46);
}
</style>



